import React, { useState, useCallback, useEffect } from "react";
import { Box, Button, TextInput, Heading, Image, Carousel } from "grommet";
import { useDropzone } from "react-dropzone";
import Parse from "../../library/parse";
import imageCompression from "browser-image-compression";
import { encodeURIComponent } from "../../helpers";
import slugify from "slugify";

const Component = ({ initialParseFile, onChange, onComplete, onUpload }) => {
  const [fileObject, setFileObject] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    initialParseFile && setFileObject(initialParseFile);
  }, [initialParseFile]);

  const uploadFile = (file) => {
    var parseFile = new Parse.File(
      slugify(file.name, { remove: /[*+~.()'"!:@]/g }),
      file,
      undefined,
      { name: file.name }
    );

    return parseFile.save()
  };

  const onDrop = useCallback(
    async (acceptedFiles) => {
      setLoading(true);
      const uploadedFiles = await Promise.all(acceptedFiles.map(async (file) => {
        const options = {
          maxSizeMB: 1,
          maxWidthOrHeight: 1920,
          useWebWorker: true,
        };
        return imageCompression(file, options).then((f) => {
          return uploadFile(f)
          .then(f => {
            onUpload && onUpload([f])
            return f;
          })
        });
      }));
      onComplete && onComplete(uploadedFiles)
      setLoading(false);
    },
    [fileObject]
  );
  const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

  return (
    <>
      <Box
        align="center"
        justify="center"
        direction="row"
        fill={true}
        background="accent-2"
      >
        <div {...getRootProps()} style={{ textAlign: "center", width: "100%" }}>
          <input {...getInputProps()} />
          {loading && <p>Uploading</p>}
          {!loading && (
            <>
              {isDragActive ? (
                <p>Yes, Drop the files here ...</p>
              ) : (
                <p>Drop the files here ...</p>
              )}
            </>
          )}
        </div>
      </Box>
    </>
  );
};

export default Component;
